.home {
    position: relative;
    padding-top: 7vh;
}

.home__bg {
    /*     background-image: url('../../images/background.jpg'); */
    background-color: white;
    width: 100%;
    height: 100vh;
    position: absolute;
    opacity: .25;
    z-index: -1;
}

.home__container {
    width: 80%;
    margin: 0 auto;
    padding-top: 3rem;
}

.home__title {
    font-size: 5rem;
    font-weight: 700;
}

.home__description {
    padding: 1.5rem 0 3rem;
    font-size: 2rem;
    font-weight: 500;
}

.home__quiz-container {
    display: grid;
    grid-template-columns: repeat(4, minmax(200px, 1fr));
    gap: 2rem;
}