.quiz {
    padding-top: 7vh;
    background: url('../../images/background.jpg');
    height: 100vh;
}

.quiz__container {
    padding-top: 3rem;
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.quiz__title {
    font-size: 3rem;
    font-weight: 700;
    text-align: center;
    background-color: white;
    display: inline;
}

.quiz__question-container {
    display: flex;
    justify-content: center;
    width: 100%;
    padding-top: 3rem;
}

.quiz__item {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    background-color: lightblue;
    border-radius: 1rem;
    width: 80%;
    gap: 1rem;
}

.quiz__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.quiz__header-number,
.quiz__header-question {
    font-size: 2rem;
    font-weight: 700;
}

.quiz__alternatives {
    padding-top: 1.5rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.quiz__option {
    padding: 1rem;
    border: 1px solid azure;
    font-weight: 700;
    background-color: azure;
    border-radius: .5rem;
}

.quiz__option:hover {
    cursor: pointer;
    background-color: cornflowerblue;
    border-color: cornflowerblue;
    color: white;
}

.quiz__btn {
    padding: .75rem 0;
    border: 1px solid white;
    border-radius: 2em;
    font-weight: 900;
    font-size: 1.25rem;
    letter-spacing: .05rem;
}

.quiz__btn:hover {
    cursor: pointer;
    border-color: cornflowerblue;
    color: white;
    background-color: cornflowerblue;
}

.timer {
    width: 90%;
    background-color: whitesmoke;
    height: 1.5rem;
    align-self: center;
    border-radius: 1rem;
    position: relative;
}

.timer__inner {
    width: 100%;
    height: 100%;
    background-color: greenyellow;
    border-radius: 1rem;
    position: absolute;
}

.score__panel {
    width: 80%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    background-color: white;
    padding: 3rem;
}

.score__title {
    font-size: 3rem;
    font-weight: 700;
}

.score__points {
    font-size: 4rem;
    font-weight: 700;
}


@keyframes timerAnim {
    0% {
        width: 100%;
        background-color: greenyellow;
    }

    50% {
        width: 50%;
        background-color: yellow;
    }

    75% {
        background-color: red;
    }

    100% {
        width: 0%;
        background-color: red;
    }
}