.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    height: 7vh;
    background-color: black;
    z-index: 1;
}

.header__container {
    height: 100%;
    display: flex;
    width: 80%;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
}

.header__link {
    text-decoration: none;
    color: white;
}

.header__link:hover {
    cursor: pointer;
}

#header__logo {
    font-size: 2rem;
}

.header__menu {
    color: white;
    height: 50%;
}

.header__menu:hover {
    cursor: pointer;
    color: cornflowerblue;
}