.score__buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

.score__btn {
    padding: 1rem 1.5rem;
    border-radius: .5rem;
    border: 1px solid white;
    font-size: 1.25rem;
    font-weight: 700;
}

#score__btn-reset {
    background-color: greenyellow;
}

#score__btn-reset:hover {
    cursor: pointer;
    background-color: green;
    color: white;
}

#score__btn-browse {
    background-color: aqua;
}

#score__btn-browse:hover {
    cursor: pointer;
    background-color: cornflowerblue;
    color: white;
}