.quizcard {
    border-radius: .5rem;
    background-color: cornflowerblue;
    text-align: center;
    color: black;
    text-decoration: none;
}

.quizcard__img-container {
    object-fit: cover;
    width: 100%;
}

.quizcard__img {
    width: 100%;
    border-top-right-radius: .5rem;
    border-top-left-radius: .5rem;
}

.quizcard__title {
    padding: 1rem 0;
    font-weight: 500;
    font-size: 2rem;
}